.theadCell {
  display: flex;
  align-content: center;
  padding: 0 0 4px 0;
}

.selected {
  display: flex;
  align-items: center;
  min-width: 75px;
  margin: 0 25px 0 0;
}

.product {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.col {
  display: flex;
  align-items: center;
}

.imgBox {
  position: relative;
  width: 52px;
  height: 52px;
  overflow: hidden;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}

.img {
  object-fit: cover;
  position: absolute;
  width: auto;
  height: auto;
  max-width: calc(100% - 8px);
  max-height: calc(100% - 8px);
  min-width: calc(100% - 8px);
  min-height: calc(100% - 8px);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.name {
  margin: 0 0 0 15px;
}

.theadButton {
  min-width: 53px;
  &:active {
    color: #fff;
    border-color: #40a9ff;
    background-color: #40a9ff;
  }
}
.theadButtonDanger {
  &:hover {
    border-color: #f5222d;
    color: #f5222d;
  }
  &:focus {
    color: #f5222d;
    border-color: #f5222d;
    background-color: transparent;
  }
  &:active {
    color: #fff;
    border-color: #f5222d;
    background-color: #f5222d;
  }
}

.buttonIcon {
  font-size: 24px;
}

.icon {
  font-size: 24px;
  color: #bfbfbf;
}
