.theadCell {
  width: calc(100% + 16px);
  display: flex;
  align-content: center;
}

.theadCellRow {
  display: flex;
  align-items: center;
}

.selected {
  display: flex;
  align-items: center;
  min-width: 75px;
  margin: 0 25px 0 0;
}

.columnsTitleBox {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
}

.planStatusColumnsTitle {
  width: 106px;
}
.arrangementColumnsTitle {
  width: 106px;
  margin: 0 18px 0 90px;
}

.product {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.col {
  display: flex;
  align-items: center;
}

.dateBox {
  display: flex;
  align-items: center;
}

.calendarIcon {
  font-size: 20px;
  color: #bfbfbf;
  margin: 0 15px 0 0;
}

.buttonBox {
  margin: 0 0 0 90px;
  min-width: 106px;
}

.posButton {
  min-width: 53px;

  &:active {
    color: #fff;
    border-color: #40a9ff;
    background-color: #40a9ff;
  }
}

.posButtonIcon {
  font-size: 20px;
}

.imgBox {
  position: relative;
  width: 52px;
  height: 52px;
  overflow: hidden;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}

.img {
  object-fit: cover;
  position: absolute;
  width: auto;
  height: auto;
  max-width: calc(100% - 8px);
  max-height: calc(100% - 8px);
  min-width: calc(100% - 8px);
  min-height: calc(100% - 8px);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.name {
  margin: 0 0 0 15px;
}

.theadButton {
  min-width: 53px;
  &:active {
    color: #fff;
    border-color: #40a9ff;
    background-color: #40a9ff;
  }
}

.theadButtonDanger {
  &:hover {
    border-color: #f5222d;
    color: #f5222d;
  }
  &:focus {
    color: #f5222d;
    border-color: #f5222d;
  }
  &:active {
    color: #fff;
    border-color: #f5222d;
    background-color: #f5222d;
  }
}

.theadButtonIcon {
  font-size: 18px;
}

.menuItem {
  cursor: default;
  width: 584px;
  &:hover {
    background-color: transparent;
  }
}

.rangePicker {
  :global {
    .ant-picker-range-arrow {
      display: none;
    }
    .ant-picker-panel-container {
      box-shadow: none;
    }
    .ant-picker-panel {
      border: none;
    }
  }
}

.form {
  padding: 0 0 0 50px;
  :global {
    .ant-input {
      width: 285px;
    }
  }
}
